@import url(https://fonts.googleapis.com/css2?family=Reggae+One&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Reggae+One&family=Shippori+Mincho:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Notable&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Abril+Fatface&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Abril+Fatface&family=Antic+Slab&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.App {
  width: 100%;
  background-image: linear-gradient(rgb(255, 255, 255), rgb(221, 221, 221));
}

