.btn {
  margin: 0;
  font-size: 15px;
  height: 40px;
  width: 140px;
  color: white;
  background-color: #1d412c;
  border: none;
  border-radius: 5px;
  margin-bottom: 25px;
}

.btn:hover {
  cursor: pointer;
  transform: scale(1.1);
  background-color: #153121;
}

.btnWide {
  width: 300px;
  height: 300px;
}

.blueText:hover {
  color: rgb(0, 136, 255);
}
.blueText {
  color: rgb(53, 103, 147);
  text-align: center;
  margin: 0 2px 10px 2px;
  padding: 5px;
  background-color: rgb(236, 236, 236);
  border-radius: 5px;
}

.BeerTile {
  display: flex;
  flex-direction: column;
  /* justify-content: space-around; */
  align-items: center;
  min-width: 128px;
  min-height: 240px;
  margin: 16px;

  background-color: rgb(255, 255, 255);
  /* background: linear-gradient(191deg, rgba(77,81,71,1) 0%, rgba(126,143,116,0.7259103470489758) 100%);  */
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.3);
}
.BeerTile:hover {
  box-shadow: 5px 5px 5px 5px rgba(10, 15, 0, 0.4);
}
.BeerTile__name {
  font-size: 20px;
  color: black;
  margin-top: 30px;
  text-align: center;
}
.BeerTile__type {
  font-size: 12px;
  color: rgb(2, 27, 2);
  margin-bottom: 30px;

  text-align: center;
}

.BeerTile__brewery {
  font-size: 13px;
  color: rgb(2, 27, 2);
  margin-bottom: 20px;
}
a {
  text-decoration: none;
  color: transparent;
}
.BeerTile__img {
  margin-bottom: 10px;
}
@media only screen and (min-width: 768px) {
  .BeerTile {
    width: 220px;
    height: 350px;
  }
}

.homeButton {
  background-color: #c84f1f;
  width: 60px;
  height: 60px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  position: fixed;
  left: 10px;
  top: 150px;
  box-shadow: 3px 3px 4px -1px rgba(0, 0, 0, 0.8);
}
.homeButton:hover {
  cursor: pointer;
  background-color: #c84f1f;
  transform: scale(1.1);
}

@media only screen and (min-width: 768px) {
  .homeButton {
    left: 30px;
    top: 100px;
  }
}

.TileContainer__p {
  padding: 15px;
  display: flex;
  justify-content: center;
  font-size: 20px;
  font-weight: 700px;
  background-color: #153121;
  color: white;
}

.TileContainer__div {
  margin: auto;
  max-width: 1400px;
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

@media only screen and (min-width: 768px) {
  .TileContainer__p {
    font-size: 20px;
  }
}

.header {
  position: -webkit-sticky;
  position: sticky;
  margin: 0;
  padding: 0;
  top: 0px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  background-color: white;
  color: white;
  box-shadow: 0px 5px 7px 1px rgba(0, 0, 0, 0.3);
  letter-spacing: 10px;
  font-size: 20px;
  height: 110px;
  font-family: "Abril Fatface", cursive;
}
.header__logo {
  color: #153121;
}
/* .header__logo:hover {
  color: white;
  text-shadow: 4px 4px 8px #000000;
} */

.header__button {
  font-size: 12px;
  width: 228px;
  height: 25px;
  margin: 0;

  color: white;
  background-color: #1d412c;
  border: none;
  border-radius: 5px;
}

.header__button:hover {
  cursor: pointer;
  transform: scale(1.1);
  background-color: #153121;
}

@media only screen and (min-width: 768px) {
  .header {
    font-size: 24px;
    letter-spacing: 24px;
    display: flex;
    flex-direction: row;
    height: 90px;
  }
  .header__button {
    font-size: 12px;
    height: 35px;
    width: 140px;
  }
} ;

.searchbox {
  margin: 0 10px;
  display: flex;
  justify-content: center;
  height: 25px;
  width: 230px;
  border-radius: 20px;
  background-color: transparent;
  border: 1px solid #153121;
  min-width: 200px;
}
.searchbox input {
  border: none;
  width: 75%;
  color: #153121;
  font-size: 10px;
}

.searchbox input:focus {
  outline: none;
}
@media only screen and (min-width: 768px) {
  .searchbox input {
    font-size: 16px;
  }
  .searchbox {
    height: 35px;
    max-width: 400px;
    width: 300px;
  }
}

.Beercard__container {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  background-image: linear-gradient(rgb(255, 255, 255), rgb(212, 212, 212));
  font-family: "Shippori Mincho", serif;
}
.Beercard {
  max-width: 1200px;
}

@media screen and (min-width: 1500px) {
  .Beercard {
    margin-top: 5%;
  }
}

.Beercard__upper {
  width: 100%;
  height: auto;
  display: flex;
  flex-direction: column;
}
.Beercard__left {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  border: none;

  padding: 15px 0 15px 0;

  color: black;
}
.Beercard__left img {
  width: 110px;
  height: auto;
}
.Beercard__left--rating {
  font-size: 22px;
  font-family: "Abril Fatface", cursive;
}
.Beercard__left--rating p {
  text-align: center;
}
.Beercard__right {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border: none;
  border-radius: 5px;
  margin: 3% 3% 0 3%;
  box-shadow: 0px 5px 7px 1px rgba(0, 0, 0, 0.3);
  color: black;
  box-shadow: 0px 5px 7px 1px rgba(0, 0, 0, 0.3);
  background-color: rgb(255, 255, 255);
}
.Beercard__right--name {
  font-family: "Abril Fatface", cursive;
  font-size: 22px;
  color: #153121;
}
.Beercard__right--info {
  padding: 5%;
}
.Beercard__right--description {
  font-size: 18px;
}
.Beercard__right--table {
  border-collapse: collapse;
  width: 100%;
  margin: 15px 0 15px 0;
  font-size: 16px;
}
.Beercard__right--table tr {
  border-bottom: 1px solid black;
}
.Beercard__comments {
  display: flex;
  align-items: flex-start;
  flex-direction: column;
  margin: 3% 3% 0 3%;
  color: black;
  border: none;
  box-shadow: 0px 5px 7px 1px rgba(0, 0, 0, 0.3);
  background-color: rgb(255, 255, 255);
  border-radius: 5px;
}
@media screen and (min-width: 1024px) {
  .Beercard__right--description {
    font-size: 18px;
  }
  .Beercard__container {
    justify-content: flex-start;
    height: 100vh;
  }
}
@media screen and (min-width: 768px) {
  .Beercard__container {
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
  }
  .Beercard__upper {
    flex-direction: row;
  }
  .Beercard__left {
    width: 40%;
    flex-direction: row-reverse;
    justify-content: center;
    padding: 2% 0 0 0;
  }
  .Beercard__right {
    width: 60%;
    margin: 2% 3% 0 3%;
  }
  .Beercard__right--info {
    padding: 1% 5% 1% 5%;
  }
  .Beercard__left img {
    width: 130px;
    height: auto;
  }

  .Beercard__comments {
    display: flex;
    flex-direction: row;
    padding: 10px;
    margin: 2% 3% 3% 3%;
  }
  .Beercard__left--rating {
    font-size: 30px;
  }
}

.Comments__box {
  width: 100%;
  color: black;
  padding-bottom: 20px;
}
.Comments__box h3 {
  font-size: 24px;
  font-family: "Abril Fatface", cursive;
  text-align: center;
  color: #153121;
  letter-spacing: 5px;
}
.Comments__box li {
  display: flex;
  justify-content: space-around;
  padding: 5px;
  list-style: none;
  background-color: rgb(236, 236, 236);
  border-radius: 5px;
  margin: 10px;
}

.commentary__container {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  font-size: 13px;
}
.commentary__login {
  font-weight: 700;
  width: 100px;
  margin-top: auto;
}

.commentary__text {
  align-self: left;
  font-weight: 700;
}
.commentary__rating {
  margin-right: 50px;
  font-weight: 700;
}
@media (max-width: 1024px) {
  .commentary__container {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    font-size: 17px;
    text-align: center;
  }
  .commentary__login {
    font-weight: 700;
    margin-top: 5px;
    text-decoration: underline;
    width: 100%;
  }

  .commentary__text {
    width: 100%;
    align-self: left;
    font-weight: 700;
    margin-top: 5px;
  }
  .commentary__rating {
    width: 100%;
    font-weight: 700;
    margin-top: 5px;
    margin-right: 0;
  }
}
@media (min-width: 768px) {
  .Comments__box h3 {
    font-size: 30px;
  }
}

.Input__box {
  display: flex;
  justify-content: center;
  width: 90%;
  /* box-shadow: 0px 5px 7px 1px rgba(0, 0, 0, 0.3); */
  margin: 2%;
  background-color: rgb(255, 255, 255);
  border-radius: 5px;
}
.Input__box--form {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.Input__box--form h3 {
  font-family: "Abril Fatface", cursive;
  text-align: center;
  color: #153121;
}
.Input__box--form input[type="radio"] {
  opacity: 0;
}

.Rating_number {
  text-align: center;
  font-size: 22px;
}
.Input__submit {
  width: 100px;
  margin: 5px;
  height: 25px;
  color: white;
  border: none;
  border-radius: 15px;
  outline: none;
  background-image: linear-gradient(
    to right,
    #6a9113 0%,
    #141517 51%,
    #6a9113 100%
  );
  text-align: center;
  text-transform: uppercase;
  transition: 0.5s;
  background-size: 200% auto;
  box-shadow: 0 0 20px #eee;
  border: 1px solid #153121;
}
.Input__submit:hover {
  background-position: right center; /* change the direction of the change here */
  color: #fff;
  text-decoration: none;
}

.Input_box {
  color: black;
  text-align: center;
  margin: 0 2px 10px 2px;
  padding: 5px;
  background-color: rgb(236, 236, 236);
  border-radius: 5px;
}

@media screen and (min-width: 768px) {
  .Input__box--form h3 {
    font-size: 24px;
  }
  .Rating__text {
    height: 50px;
    width: 300px;
    padding: 4px;
  }
  .Input__box {
    width: 70%;
  }
  .Input__submit {
    border-radius: 15px;
    width: 140px;
    height: 30px;
    margin: 5px;
  }
}

.wrapper {
  padding: 10px;
}

.adder__text {
  border: 1px solid black;
  margin: 5px;
}

.form {
  margin: auto;
  margin-top: 100px;
  display: flex;
  align-items: stretch;
  justify-content: center;
  flex-direction: column;
  border: 2px solid #153121;
  border-radius: 10px;
  min-height: 100px;
  max-width: 500px;
  background-color: white;
  box-shadow: 5px 5px 5px 5px rgba(223, 226, 218, 0.4);
  padding: 20px;
}
.loginWrapper {
  display: flex;
  height: calc(100vh - 110px);
}

.signForm__button2 {
  margin: auto;
  margin-top: 10px;
  background-color: transparent;
  border: none;
  cursor: pointer;
  color: blue;
}
.signForm__button2:hover {
  color: red;
}

@media only screen and (min-width: 768px) {
  .form {
    margin: auto;
    min-width: 500px;
    margin-top: 12%;
  }
  .loginWrapper {
    display: flex;
    height: calc(100vh - 90px);
  }
}

.formInput {
  display: flex;
  flex-direction: column;
  font-size: 15px;
  align-items: stretch;
  justify-content: center;
  border: 1px solid #153121;
  border-radius: 10px;
  min-height: 25px;
  width: 90%;
  background-color: white;

  box-shadow: 5px 5px 5px 5px rgba(223, 226, 218, 0.4);
  cursor: pointer;
}

.login__submit:hover {
  color: red;
}
h2 {
  color: rgb(21, 49, 33);
}

.login__submitFormInput {
  background-image: linear-gradient(
    to right,
    #6a9113 0%,
    #141517 51%,
    #6a9113 100%
  );
}
.login__submitFormInput {
  margin-top: 15px;
  text-align: center;
  text-transform: uppercase;
  transition: 0.5s;
  background-size: 200% auto;
  color: white;
  box-shadow: 0 0 20px #eee;
  display: flex;
  flex-direction: column;
  font-size: 15px;
  align-items: stretch;
  justify-content: center;
  border: 1px solid #153121;
  border-radius: 10px;
  min-height: 25px;
  width: 90%;
}

.login__submitFormInput:hover {
  background-position: right center; /* change the direction of the change here */
  color: #fff;
  text-decoration: none;
}

